
import { useEffect, useState } from "react";
import {Link,useParams} from "react-router-dom";
import data from './port.json';

function Detail({contact}) {
    contact('0');

    const {id} = useParams();
    let [work, setWork] = useState(null);

    useEffect(()=>{

        data.port.map(function(v){
            //console.log(v.num)
            if(v.num == id) {
                setWork(v);
            }
          
        })

        window.scrollTo(0,0);
        
    },[])


    return (
        <>
            <div className="detail_w black">
                <div className="bg"><img src={work?.img1} alt="" /></div>
                <div className="con_w">
                    <div className="g_bg">
                        <div className="btn_w"><Link to="/Port"></Link></div>
                        <div className="con">
                            <div className="txt">
                                <h3>{work?.title}</h3>
                                <p>
                                    <span>Publishing : {work?.publishing}</span>
                                    <span>Design : {work?.design}</span><br />
                                    <span>Working period : {work?.date}</span>
                                </p>
                                <p>{work?.comment}</p>
                                <a href={work?.link} target="_blank"></a>
                            </div>
                            <div className="img">
                                <img src={work?.img1} alt="" />
                                <img src={work?.img2} alt="" />
                            </div>
                        </div>
                    </div>
                </div>
            </div>  
        </>
    );
}


export default Detail;