
import '../css/style.css';
import data from './port.json';
import Thumb from './Thumb';
import Slide  from './Slide';
// import Detail from './Detail';
import {Link} from "react-router-dom";
import {useRef, useState, useEffect} from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import SwiperCore, { Navigation } from "swiper";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'


function Port({contact}) {
    contact('1');
    
    let [thumb, setThumb] = useState(),
        [title, setTitle] = useState(),
        [device, setDevice] = useState(),
        [date, setDate] = useState(),
        [design, setDesign] = useState(),
        [publishing, setPublishing] = useState(),
        [lang, setLang] = useState();
    
    useEffect(()=>{
        window.scrollTo(0,0);
        let head = document.querySelector('header');
        head.classList.remove('active');
   
        btn();
    },[])

    const [onoff,setOnoff] = useState('none');
    const [listoff, setListoff] = useState('none');
    const click = document.querySelector('.click');
    const [btns, setBtns] = useState('');
    const [lists, setLists] = useState('');

    function btn(){
        setOnoff('block');
        setListoff('none');
        setBtns('_on');
        setLists('');
    }
        
    function base(){
        setOnoff('none');
        setListoff('block');
        setLists('_on');
        setBtns('');
    }
    
    
    
  return (
    <>
    <div className="list_w black">
        <div className="inner">
            <div className="tit">
                <h2>Port-<br /><span>folio</span></h2>
                <p>   .<br />   .</p>
                <div className="btns">
                    <span className="click" onClick={btn}><img src={`img/images${btns}.svg`} alt="" /></span>
                    <span className="click" onClick={base}><img src={`img/list${lists}.svg`} alt="" /></span>
                </div>
            </div>
        </div>
        <div className="slide_w">
            {onoff == 'block' ? <Slide /> : ''}
        </div>
        <div className="inner">
            {listoff == "block" ? <div className="list">
                
                {
                    data.port.map(function(v,i){
                        return <Thumb info={v} k={thumb} key={i} />
                    })
                    
                }
            </div> : ''}
        </div>
    </div>
    
    </>
  );
}

export default Port;