
import '../css/style.css';
import {HashRouter, Route, Routes, Link,useLocation} from "react-router-dom";
import { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from "swiper/react";
import { EffectFade, Autoplay } from 'swiper';
import 'swiper/css';
import "swiper/css/effect-fade";

function Main() {


  return (
    <>
    <main>
        <div className="content">
            <div className="visual cnt01">
            <Swiper
            loop={true}
            effect={"fade"}
            spaceBetween={0}
            slidesPerView={1}
            onSlideChange={() => console.log('slide change')}
            onSwiper={(swiper) => console.log(swiper)}
            autoplay={{
                delay: 2500,
                disableOnInteraction: false,
                }}
            modules={[EffectFade,Autoplay]}
            >
      <SwiperSlide><img src="img/visual_01.jpg" /></SwiperSlide>
      <SwiperSlide><img src="img/visual_02.jpg" /></SwiperSlide>
      <SwiperSlide><img src="img/visual_03.jpg" /></SwiperSlide>
      <SwiperSlide><img src="img/visual_04.jpg" /></SwiperSlide>
      <SwiperSlide><img src="img/visual_05.jpg" /></SwiperSlide>
    </Swiper>
                <div className="per"><img src="img/visual_person.png" alt="" /></div>
                <div className="tit">
                    <h2><div>Publisher</div> <div><span>&amp; Front-end</span></div></h2>
                    <h3><div><img src="img/powerwmj.svg" alt="powerwmj" /></div></h3>
                    <p>      .<br />  <span>  </span>. -  -</p>
                </div>
            </div>
        </div>
    </main>
    </>
  );
}

export default Main;