
import Detail from './Detail';
import {Link} from "react-router-dom";

function Thumb({info}){
    

    return(
        <>
        <Link to={`/Detail/${info.num}`}>
        <div className="thumb">
            <div className="img"><img src={info.thumb} alt="" /></div>
            <div className="txt">
                <h3>{info.title}</h3>
                <p className="device">{info.device}</p>
                <p className="date">
                    {info.date}<br />
                    <span>Design : {info.design}</span>         <span>Publishing : {info.publishing}</span><br />
                    {info.lang}
                </p>
            </div>
        </div>
        </Link>
        </>
    )
}
export default Thumb;