import './css/style.css';
import Main from './component/Main';
import Port from './component/Port';
import About from './component/About';
import {HashRouter, Route, Routes, Link} from "react-router-dom";
import { useEffect, useState, useRef } from 'react';
import Detail from './component/Detail';
import Slide from './component/Slide';

function App() {

  const [num,setNum] = useState(0); 
  
  useEffect (()=>{
    let imgs = new Array("./img/visual_01.jpg","./img/visual_02.jpg","./img/visual_03.jpg","./img/visual_04.jpg","./img/visual_05.jpg");
    let inter = setTimeout(()=>{
            (num==(imgs.length-1)) ? setNum(0) : setNum(num+1);
    },2500);
  })

  // scroll
  const container = useRef();
  
    useEffect( ()=>{

      const link = document.querySelectorAll('.menu li');
      const logo = document.querySelector('h1');
      link.forEach(function(v,k){
        v.onclick = function(){
          link[0].classList.remove('on')
          link[1].classList.remove('on')
          link[2].classList.remove('on')
          v.classList.add('on')
        }
      
        logo.onclick = function(){
          link[0].classList.add('on')
          link[1].classList.remove('on')
          link[2].classList.remove('on')
        }
      })
      
        

      window.addEventListener('scroll', scrollFun);
      return () => {
        window.removeEventListener('scroll', scrollFun);
       
        

      };
      
    }
    
  ,[]);

  
  

  function scrollFun(){
    let head = document.querySelector('header');
    let title = document.querySelector('h2');
    const more = document.querySelector('.more');
    if(title.offsetTop < window.scrollY){
        head.classList.add('active');
      } else {
        head.classList.remove('active');
        more.onclick = function(){
          head.classList.remove('active');
        }
      }

      
  }
  
const [none, setNone] = useState('');

  function page(n){
    //console.log(n);
    if(n == '0') {
      setNone('none');
    } else {
      setNone('');
    }
  }

  
  


  

  return (
    <HashRouter>
      <header className={none}>
        <h1><Link to="./"><img src="./img/logo.png" alt="powerwmj" /></Link></h1>
        <div className="menu">
            <ul>
                <li className="on"><Link to="./">Main</Link></li>
                <li><Link to="./Port">Portfolio</Link></li>
                <li><Link to="./About">About PWJ</Link></li>
            </ul>
            <p><span className="more"><img src="./img/btn_more.png" alt="" /></span></p>
        </div>
    </header>
    
     
    <Routes>
      <Route path="/" element={<Main k={num}/>} />
      <Route path="/Port" element={<Port contact={page} />} />
      <Route path="/About" element={<About contact={page} />} />
      <Route path="/Detail/:id" element={<Detail contact={page} />} />
      <Route phth="/Slide" element={<Slide /> } />
    </Routes>
    
    </HashRouter>
  );
}

export default App;
